// paneldynamic.requiredIf: "Use the magic wand icon to set a conditional rule that prevents survey submission unless at least one nested question has an answer." => "Brug tryllestavsikonet til at angive en betinget regel, der forhindrer indsendelse af undersgelser, medmindre mindst t indlejret sprgsml har et svar."
// paneldynamic.templateQuestionTitleLocation: "Applies to all questions within this panel. If you want to override this setting, define title alignment rules for individual questions. The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Top\" by default)." => "Glder for alle sprgsml i dette panel. Hvis du vil tilsidestte denne indstilling, skal du definere regler for titeljustering for individuelle sprgsml. Indstillingen \"Nedarv\" anvender indstillingen p sideniveau (hvis angivet) eller undersgelsesniveau (\"Top\" som standard)."
// paneldynamic.templateErrorLocation: "Sets the location of an error message in relation to a question with invalid input. Choose between: \"Top\" - an error text is placed at the top of the question box; \"Bottom\" - an error text is placed at the bottom of the question box. The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Top\" by default)." => "Angiver placeringen af en fejlmeddelelse i forhold til et sprgsml med ugyldigt input. Vlg mellem: \"Top\" - en fejltekst placeres verst i sprgsmlsfeltet; \"Nederst\" - en fejltekst placeres nederst i sprgsmlsfeltet. Indstillingen \"Nedarv\" anvender indstillingen p sideniveau (hvis angivet) eller undersgelsesniveau (\"Top\" som standard)."
// paneldynamic.errorLocation: "Sets the location of an error message in relation to all questions within the panel. The \"Inherit\" option applies the page-level (if set) or survey-level setting." => "Angiver placeringen af en fejlmeddelelse i forhold til alle sprgsml i panelet. Indstillingen \"Nedarv\" anvender indstillingen for sideniveau (hvis angivet) eller undersgelsesniveau."
// paneldynamic.page: "Repositions the panel to the end of a selected page." => "Flytter panelet til slutningen af en markeret side."
// paneldynamic.innerIndent: "Adds space or margin between the panel content and the left border of the panel box." => "Tilfjer mellemrum eller margen mellem panelindholdet og panelboksens venstre kant."
