// question.requiredIf: "Use the magic wand icon to set a conditional rule that prevents survey advancing or submission unless the question received an answer." => "Brug tryllestavsikonet til at angive en betinget regel, der forhindrer undersgelse i at g videre eller blive sendt, medmindre sprgsmlet blev besvaret."
// question.startWithNewLine: "Unselect to display the question in one line with the previous question or panel. The setting doesn't apply if the question is the first element in your form." => "Fjern markeringen for at f vist sprgsmlet p n linje med det forrige sprgsml eller panel. Indstillingen glder ikke, hvis sprgsmlet er det frste element i formularen."
// question.page: "Repositions the question to the end of a selected page." => "Flytter sprgsmlet til slutningen af en markeret side."
// question.state: "Choose from: \"Expanded\" - the question box is displayed in full and can be collapsed; \"Collapsed\" - the question box displays only the title and description and can be expanded; \"Locked\" - the question box is displayed in full and cannot be collapsed." => "Vlg mellem: \"Udvidet\" - sprgsmlsfeltet vises fuldt ud og kan skjules; \"Skjult\" - sprgsmlsfeltet viser kun titlen og beskrivelsen og kan udvides; \"Lst\" - sprgsmlsfeltet vises fuldt ud og kan ikke skjules."
// question.titleLocation: "Overrides title alignment rules defined on a panel, page or survey level. The \"Inherit\" option applies any higher-level settings (if set) or survey-level setting (\"Top\" by default)." => "Tilsidestter regler for titeljustering, der er defineret p panel-, side- eller undersgelsesniveau. Indstillingen \"Nedarv\" anvender alle indstillinger p hjere niveau (hvis indstillet) eller indstilling p undersgelsesniveau (\"Top\" som standard)."
// question.descriptionLocation: "The \"Inherit\" option applies the survey-level setting (\"Under the question title\" by default)." => "Indstillingen \"Nedarv\" anvender indstillingen p undersgelsesniveau (\"Under sprgsmlets titel\" som standard)."
// question.errorLocation: "Sets the location of an error message in relation to the question with invalid input. Choose between: \"Top\" - an error text is placed at the top of the question box; \"Bottom\" - an error text is placed at the bottom of the question box. The \"Inherit\" option applies the survey-level setting (\"Top\" by default)." => "Angiver placeringen af en fejlmeddelelse i forhold til sprgsmlet med ugyldigt input. Vlg mellem: \"Top\" - en fejltekst placeres verst i sprgsmlsfeltet; \"Nederst\" - en fejltekst placeres nederst i sprgsmlsfeltet. Indstillingen \"Nedarv\" anvender indstillingen p undersgelsesniveau (\"Top\" som standard)."
// question.indent: "Adds space or margin between the question content and the left border of the question box." => "Tilfjer mellemrum eller margen mellem sprgsmlsindholdet og sprgsmlsfeltets venstre kant."
// question.width: "Sets the width of the question in proportion to other survey elements in the same line. Accepts CSS values (px, %, in, pt, etc.)." => "Angiver bredden af sprgsmlet i forhold til andre undersgelseselementer p samme linje. Accepterer CSS-vrdier (px, %, in, pt osv.)."
// surveyvalidator.expression: "Use the magic wand icon to set a validation rule for the question." => "Brug tryllestavsikonet til at angive en valideringsregel for sprgsmlet."
// question.textUpdateMode: "Choose from: \"On lost focus\" - the value is updated when the input field loses focus; \"While typing\" - the value is updated in real-time, as users are typing. The \"Inherit\" option applies the survey-level setting (\"On lost focus\" by default)." => "Vlg mellem: \"Ved tabt fokus\" - vrdien opdateres, nr indtastningsfeltet mister fokus; \"Mens du skriver\" - vrdien opdateres i realtid, da brugerne skriver. Indstillingen \"Nedarv\" anvender indstillingen p undersgelsesniveau (\"Ved mistet fokus\" som standard)."
// question.url: "You can use any web service as a data source for multiple-choice questions. To populate choice values, enter the URL of the service providing the data." => "Du kan bruge enhver webtjeneste som datakilde til multiple choice-sprgsml. Hvis du vil udfylde valgvrdier, skal du angive URL-adressen p den tjeneste, der leverer dataene."
// question.searchMode: "A comparison operation used to filter the drop-down list." => "En sammenligningshandling, der bruges til at filtrere rullelisten."
// signaturepad.signatureWidth: "Sets the width of the displayed signature area and the resulting image." => "Indstiller bredden p det viste signaturomrde og det resulterende billede."
