// signaturepad.signatureAutoScaleEnabled: "Select if you want the signature area to fill all available space within the question box while maintaining the default 3:2 aspect ratio. When custom width and height values are set, the setting will keep the aspect ratio of these dimensions." => "Vlg, om signaturomrdet skal udfylde al tilgngelig plads i sprgsmlsfeltet, samtidig med at standardformatet 3:2 bevares. Nr brugerdefinerede bredde- og hjdevrdier er angivet, bevarer indstillingen hjde-bredde-forholdet for disse dimensioner."
// file.imageHeight: "Adjusts the height of the image in the survey results." => "Justerer billedets hjde i undersgelsesresultaterne."
// file.imageWidth: "Adjusts the width of the image in the survey results." => "Justerer billedets bredde i undersgelsesresultaterne."
// imagepicker.imageHeight: "Overrides the minimum and maximum height values." => "Tilsidestter minimum- og maksimumhjdevrdierne."
// imagepicker.imageWidth: "Overrides the minimum and maximum width values." => "Tilsidestter vrdierne for minimum- og maksimumbredde."
// imagepicker.choices: "\"Value\" serves as an item ID used in conditional rules; \"Text\" is displayed to respondents." => "\"Vrdi\" fungerer som et vare-id, der bruges i betingede regler; \"Tekst\" vises til respondenterne."
// text.size: "This setting only resizes the input field and doesn't affect the width of the question box. To limit the accepted input length, go to <b>Validation  Maximum character limit</b>." => "Denne indstilling ndrer kun strrelsen p inputfeltet og pvirker ikke bredden af sprgsmlsfeltet. Hvis du vil begrnse den accepterede inputlngde, skal du g til <b>Validering  Maksimal tegngrnse</b>."
// comment.rows: "Sets the number of displayed lines in the input field. If the input takes up more lines, the scroll bar will appear." => "Indstiller antallet af viste linjer i indtastningsfeltet. Hvis indgangen fylder flere linjer, vises rullepanelet."
