// panel.gridLayoutColumns: "This table lets you configure each grid column within the panel. It automatically sets the width percentage for each column based on the maximum number of elements in a row. To customize the grid layout, manually adjust these values and define the title width for all questions in each column." => "Denne tabel giver dig mulighed for at konfigurere hver gitterkolonne i panelet. Den indstiller automatisk breddeprocenten for hver kolonne baseret p det maksimale antal elementer i en rkke. Hvis du vil tilpasse gitterlayoutet, skal du manuelt justere disse vrdier og definere titelbredden for alle sprgsml i hver kolonne."
// pehelp.gridLayoutEnabled: "Survey Creator allows you to manually adjust the inline widths of form elements to control the layout. If this doesn't produce the desired outcome, you can enable the grid layout, which structures form elements using a column-based system. To configure layout columns, select a page or panel and use the \"Question Settings\"  \"Grid columns\" table. To adjust how many columns a question spans, select it and set the desired value in the \"Layout\"  \"Column span\" field." => "Survey Creator giver dig mulighed for manuelt at justere de indbyggede bredder af formularelementer for at styre layoutet. Hvis dette ikke giver det nskede resultat, kan du aktivere gitterlayoutet, som strukturerer formularelementer ved hjlp af et kolonnebaseret system. For at konfigurere layoutkolonner skal du vlge en side eller et panel og bruge tabellen \"Sprgsmlsindstillinger\"  \"Gitterkolonner\". For at justere, hvor mange kolonner et sprgsml strkker sig over, skal du vlge det og indstille den nskede vrdi i feltet \"Layout\"  \"Kolonnespnd\"."
// question.effectiveColSpan: "Specifies how many columns this question spans within the grid layout." => "Angiver, hvor mange kolonner dette sprgsml strkker sig over i gitterlayoutet."
// page.gridLayoutColumns: "This table lets you configure each grid column on the page. It automatically sets the width percentage for each column based on the maximum number of elements in a row. To customize the grid layout, manually adjust these values and define the title width for all questions in each column." => "I denne tabel kan du konfigurere hver gitterkolonne p siden. Den indstiller automatisk breddeprocenten for hver kolonne baseret p det maksimale antal elementer i en rkke. Hvis du vil tilpasse gitterlayoutet, skal du manuelt justere disse vrdier og definere titelbredden for alle sprgsml i hver kolonne."

